import React, { useMemo } from 'react'
import { AiOutlineMenu, AiOutlineClose } from "react-icons/ai";
import {
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerContent,
  useDisclosure,
  Icon,
  Flex,
  Link,
} from "@chakra-ui/react"

import Box from '../components/Box';
import Button from '../components/Button';
import theme, { Media, responsive } from '../components/ThemeProvider/theme';
import { trackClick } from '../utils/trackEvent';

const links = [
  {
    name: '',
    to: 'ESG-green'
  },
  {
    name: '',
    to: 'section-subject'
  },
  {
    name: '',
    to: 'section-fund'
  },
]

const offset = 78

const Menu = ({ dark, modalonOpen }) => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  return (
    <>
      <Media greaterThanOrEqual="tablet">
        <Flex color={dark ? 'custom.headerTextGreen' : 'white'} alignItems="center">
          {links.map(({ name, to }, i) => (
            <Box.Relative cursor="pointer" fontSize={responsive('1em', null, '1.125em')} fontWeight="bold" mx={responsive(0, '0.5rem', '1.875rem')} key={i}>
              <Link
                onClick={() => {
                  trackClick(name)
                  window.scrollBy({ behavior: 'smooth', top: document.getElementById(to).getBoundingClientRect().top - offset })
                }}
              >{name}</Link>
            </Box.Relative>
          ))}
          <Box ml={responsive(0, '0.5rem', '1.875rem')} mr='1.875rem'>
            <Button.Orange
              to="section-buy"
              fontSize={responsive('1em', null, "1.125em")}
              borderRadius="1.25em"
              onClick={() => {
                trackClick('')
                window.scrollBy({ behavior: 'smooth', top: document.getElementById('section-buy').getBoundingClientRect().top - offset })
              }}
            ></Button.Orange>
          </Box>
        </Flex>
      </Media>
      <Media lessThan="tablet">
        <Box.Relative
          as={Button}
          borderRadius="0"
          bg={isOpen ? 'custom.blue' : 'transparent'}
          color={isOpen ? 'white' : dark ? 'custom.blue' : 'white'}
          _hover={{
            bg: isOpen ? 'custom.blue' : 'transparent',
            color: isOpen ? 'white' : dark ? 'custom.blue' : 'white',
          }}
          onClick={() => {
            if(isOpen) {
              onClose()
            } else {
              onOpen()
            }
          }}
          height={theme.headerHeight}
          width={theme.headerHeight}
          px="0"
          aria-label=""
        >
          <Icon as={isOpen ? AiOutlineClose : AiOutlineMenu} w={responsive('1.25rem', '2rem')} h={responsive('1.25rem', '2rem')} />
        </Box.Relative>
        <Drawer
          isOpen={isOpen}
          placement="right"
          onClose={onClose}
          size="md"
          preserveScrollBarGap
          returnFocusOnClose={false}
        >
          <DrawerContent bg="custom.blue" style={{ top: theme.headerHeight }}>
            <DrawerBody height="100%">
              {links.map(({ name, to }, i) => (
                <Box key={i}>
                  <Button.Transparent
                    width="100%"
                    py="1.5rem"
                    borderBottom="1px solid white"
                    borderRadius="0"
                    onClick={() => {
                      trackClick(name)
                      window.scrollBy({ behavior: 'smooth', top: document.getElementById(to).getBoundingClientRect().top - offset })
                      onClose()
                    }}
                  >{name}</Button.Transparent>
                </Box>
              ))}
            </DrawerBody>
            <DrawerFooter>
              <Button.Orange
                width="100%"
                fontSize="1em"
                py="1rem"
                onClick={() => {
                  trackClick('')
                  window.scrollBy({ behavior: 'smooth', top: document.getElementById('section-buy').getBoundingClientRect().top - offset })
                  onClose()
                }}
              ></Button.Orange>
            </DrawerFooter>
          </DrawerContent>
        </Drawer>
      </Media>
    </>
  )
}

export default Menu
